import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CommandBarButton, CommandBarButtonStyle } from '../../common/command-bar/command-bar.component';
import { PortalService } from '../../service/portal.service';
import { Observable } from 'rxjs';

@Component({
    selector: 'app-step3',
    templateUrl: './step3.component.html',
    styleUrls: ['./step3.component.sass']
})
export class Step3Component implements OnInit {
    commandBarButtons: CommandBarButton[] = [];
    scopeName = 'portal.appointment.step3';

    constructor(
        private _portalService: PortalService,
        private _router: Router) {

        this._portalService.setPageTitle('portal.landing.title');

        this.commandBarButtons.push({
            content: {
                scopeName: this.scopeName,
                label: 'btn.continue'
            },
            style: CommandBarButtonStyle.Positive,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onContinueButtonClick();
                    observer.next();
                })
                return observable;
            }
        },{
            content: {
                scopeName: this.scopeName,
                label: 'btn.abort'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onAbortButtonClick();
                    observer.next();
                })
                return observable;
            }
        });
    }

    ngOnInit() { }
    
    onAbortButtonClick() {
        this._router.navigate(['/landing/welcome']);
    }
    
    onContinueButtonClick() {
        this._router.navigate(['/cancellation/confirmation']);
    }
    
}
